// variables
$one: 1;
$two:2;
$three:3;
$four: 4;
$five: 5;
$six: 6;
$seven: seven;
$eight: 8;
$nine: 9;
$ten: 10;
$eleven: 11;
$twelve: 12;
$thirteen: 13;
$fourteen: 14;
$fifteen: 15;
$sixteen: 16;


@supports (display: grid) {
  .grid {
    display: grid;
  }
}

@supports not (display: grid) {
  .grid {
    display: flex;
  }
}

/* wrap all grid items in this parent */
.grid-gap__#{$ten} {
  grid-gap: #{$ten}px;
}

.grid-column-gap__30 {
  grid-column-gap: 30px;
}

.grid-row-gap__40 {
  grid-row-gap: 40px;
}

.grid-column-gap__30 {
  grid-column-gap: 30px;
}

// DEFINE -- equal grid columns here
  .center {
    align-items: center;
  }
  
  .grid-center {
    display: grid;
    align-items: center;
    align-self: center;
  }
  
  .equal-columns__#{$two} {
    grid-template-columns: repeat(2, 1fr);
  }

  .equal-columns__#{$three} {
    grid-template-columns: repeat(3, 1fr);
  }

  .equal-columns__#{$four} {
    grid-template-columns: repeat(4, 1fr);
  }

  .equal-columns__#{$five} {
    grid-template-columns: repeat(5, 1fr);
  }

  .equal-columns__#{$six} {
    grid-template-columns: repeat(6, 1fr);
  }

  .equal-columns__#{$seven} {
    grid-template-columns: repeat(7, 1fr);
  }

  .equal-columns__#{$eight} {
    grid-template-columns: repeat(8, 1fr);
  }

  .equal-columns__#{$nine} {
    grid-template-columns: repeat(9, 1fr);
  }

  .equal-columns__#{$ten} {
    grid-template-columns: repeat(10, 1fr);
  }

  .equal-columns__#{$eleven} {
    grid-template-columns: repeat(11, 1fr);
  }

  .equal-columns__#{$twelve} {
    grid-template-columns: repeat(12, 1fr);
  }

  .equal-columns__#{$thirteen} {
    grid-template-columns: repeat(13, 1fr);
  }

  .equal-columns__#{$fourteen} {
    grid-template-columns: repeat(14, 1fr);
  }

  .equal-columns__#{$fifteen} {
    grid-template-columns: repeat(15, 1fr);
  }

  .equal-columns__#{$sixteen} {
    grid-template-columns: repeat(16, 1fr);
  }

  .equal-columns__auto {
    grid-template-columns:  repeat(auto-fit, minmax(200px, 1fr));
  }

  .small-equal-columns__auto {
    grid-template-columns:  repeat(auto-fit, minmax(15%, 1fr));
  }

  .medium-equal-columns__auto {
    grid-template-columns:  repeat(auto-fit, minmax(25%, 1fr));
  }

  .large-equal-columns__auto {
    grid-template-columns:  repeat(auto-fit, minmax(33.33%, 1fr));
  }

  .extra-large-equal-columns__auto {
    grid-template-columns:  repeat(auto-fit, minmax(50%, 1fr));
  }

// define grid columns that reverse on mobile view
  .equal-columns__#{$two}-reverse {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "left right";

  }

  .right {
    grid-area: right;
  }

  .left {
    grid-area: left;
  }

// DEFINE -- unequal grid columns here
.grid-auto__columns {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
}

// START   comment
  .grid-auto__columns {
    .one {
      grid-column: span 1;
    }
    
    .two {
      grid-column: span 2;
    }
  
    .three {
      grid-column: span 3;
    }

    .four {
      grid-column: span 4;
    }

    .five {
      grid-column: span 5;
    }

    .six {
      grid-column: span 6;
    }

    .seven {
      grid-column:  span 7;
    }

    .eight {
      grid-column: span 8;
    }

    .nine {
      grid-column:  span 9;
    }

    .ten {
      grid-column: span 10;
    }

    .eleven {
      grid-column: span 11;
    }

    .twelve {
      grid-column:  span 12 ;
    }

    .thirteen {
      grid-column: span 13;
    }

    .fourteen {
      grid-column: span 14;
    }

    .fifteen {
      grid-column:  span 15;
    }

    .sixteen {
      grid-column: span 16;
    }
  }

// END comment
// GRID -- rows 


.grid {
  .rows__#{$two} {
    grid-template-rows: #{$two};
    grid-row-gap: 10px;
  }

  .rows__#{$four} {
    grid-template-rows: #{$four};
    grid-row-gap: 10px;
  }
}

@media only screen and(max-width:768px) {

.equal-columns__#{$two},
.equal-columns__#{$three},
.equal-columns__#{$four},
.equal-columns__#{$five},
.equal-columns__#{$six},
.equal-columns__#{$seven},
.equal-columns__#{$eight},
.equal-columns__#{$nine},
.equal-columns__#{$ten},
.equal-columns__#{$eleven},
.equal-columns__#{$twelve} {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-row-gap: 20px;
}

.grid-auto__columns {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, auto));
  }

  .equal-columns__#{$two}-reverse {
    grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    grid-template-areas: "right" "left";
  }
}


@media only screen and(max-width:360px) {

  .equal-columns__#{$two},
  .equal-columns__#{$three},
  .equal-columns__#{$four},
  .equal-columns__#{$five},
  .equal-columns__#{$six},
  .equal-columns__#{$seven},
  .equal-columns__#{$eight},
  .equal-columns__#{$nine},
  .equal-columns__#{$ten},
  .equal-columns__#{$eleven},
  .equal-columns__#{$twelve} {
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
  }
  
  .grid-auto__columns {
    display: grid;
    grid-template-columns: 1fr;
    }
  }